// Mock `widget` object for local development
if (!window.widget) {
  console.log("[Local Mock] Creating mock widget object.");
  const Widget = function () {
    this.uwaUrl = window.location.origin + "/";
    let events = {};
    this.addEvent = (event, callback) => {
      events[event] = callback;
      if (event === "onLoad") {
        if (document.readyState === "loading") {
          window.addEventListener("DOMContentLoaded", callback);
        } else {
          callback();
        }
      }
    };
    this.setTitle = (title) => {
      document.title = title;
    };
    this.dispatchEvent = () => {};
  };
  window.widget = new Widget();
}

// Mock `define` function for AMD modules
if (typeof define === "undefined") {
  console.log("[Local Mock] Creating mock define function.");
  window.define = (name, deps, callback) => {
    if (typeof deps === "function") {
      callback = deps;
    }
    if (!name) {
      console.error("[Local Mock] Module name is missing.");
      return;
    }
    console.log(`[Local Mock] Defining module: ${name}`);
    callback();
  };
}

// Mock `requirejs` for loading modules
if (!window.requirejs) {
  console.log("[Local Mock] Creating mock requirejs function.");
  window.requirejs = (modules, callback) => {
    console.warn("[Local Mock] Mocking RequireJS. Modules:", modules);
    callback(); // Ensure execution continues
  };
}

// Mock `DS` namespace for Drag-and-Drop
if (!window.DS) {
  window.DS = {
    DataDragAndDrop: {
      createProxy: () => ({
        addEvent: (event, callback) => {
          console.log(`[Local Mock] Drag-and-Drop event registered: ${event}`);
          if (event === "drop") callback({ data: "Mock drag-and-drop data" });
        },
      }),
    },
  };
  console.log("[Local Mock] DS namespace and DataDragAndDrop mocked.");
}

// Mock `__webpack_public_path__`
if (typeof __webpack_public_path__ === "undefined") {
  window.__webpack_public_path__ = "./";
  console.log("[Local Mock] Webpack public path set to './'.");
}

console.log("[Local Mock] Mock environment initialized.");
