const Widget = function () {
  const events = {};
  const widgetUrl = window.location.href;

  this.uwaUrl = "./";

  // Add event listener
  this.addEvent = (event, callback) => {
    console.log(`[Widget] Event registered: ${event}`);
    events[event] = callback;
    if (event === "onLoad") {
      if (document.readyState === "loading") {
        window.addEventListener("DOMContentLoaded", callback);
      } else {
        callback();
      }
    }
  };

  this.setTitle = (title) => {
    document.title = title;
    console.log(`[Widget] Title set to: ${title}`);
  };
};

// Initialize widget for 3D Dashboard
export const initWidget = (cbOk, cbError) => {
  if (window.widget) {
    console.log("[Widget] Using existing widget object.");
    cbOk(window.widget);
  } else {
    console.error("[Widget] Widget object not available.");
    cbError(new Error("Widget object not available."));
  }
};

// Drag-and-Drop Integration for 3D Dashboard
export const initDragAndDrop = () => {
  require(["DS/DataDragAndDrop/DataDragAndDrop"], (DataDragAndDrop) => {
    console.log("[Widget] DataDragAndDrop Module Loaded:", DataDragAndDrop);

    const proxy = DataDragAndDrop.createProxy();

    proxy.addEvent("dragenter", (event) => {
      console.log("[Widget] Drag enter:", event);
    });

    proxy.addEvent("dragover", (event) => {
      console.log("[Widget] Drag over:", event);
    });

    proxy.addEvent("drop", (event) => {
      console.log("[Widget] Drop:", event);
    });
  });
};

export default {
  initWidget,
  initDragAndDrop,
};
